      #solutions-benefits .benefit-img {
        height: 450px; } }
    @media (max-width: 970px) {
      #solutions-benefits .benefit-img {
        text-shadow: 0 0.2rem 0.4rem rgba(0, 0, 0, 0.5); } }
  #solutions-benefits .grid-large-column-3 {
    margin-top: 8rem;
    margin-bottom: 4rem;
    height: 10rem; }
    #solutions-benefits .grid-large-column-3:not(:nth-child(3)) {
      /*border-right: 2px solid color('dark-blue');*/
      margin-right: 14rem; }
      @media (max-width: 970px) {
        #solutions-benefits .grid-large-column-3:not(:nth-child(3)) {
          border-right: 0px;
          border-bottom: 1px solid #fff; } }
    @media (min-width: 970px) {
      #solutions-benefits .grid-large-column-3 {
        padding-top: 24%;
        border-radius: 50%;
